import { Box, ListItem, Typography } from "@rp/react/web/mui/core"
import { Lock } from "@rp/react/web/mui/icons"

type Props = {
  icon: React.ReactNode
  label: string
  handle: string
  description: string
  onClick: () => void
  blocked: boolean
}
const DatasetOption = (props: Props) => {
  return (
    <ListItem
      sx={{
        display: "flex",
        alignItems: "center",
        gap: 1.5,
        paddingInline: 2,
        paddingBlock: 1.5,
        cursor: "pointer",
        backgroundColor: "elevation.dp01",
        transition: "background-color 0.2s",
        "&:hover": {
          backgroundColor: "overlay.surface",
        },

        "& svg": {
          color: "primary.main",
        },
      }}
      onClick={event => {
        event.preventDefault()
        event.stopPropagation()
        props.onClick()
      }}
    >
      <Box
        sx={{
          backgroundColor: "BDprimary.900",
          display: "flex",
          height: 32,
          width: 32,
          alignItems: "center",
          justifyContent: "center",
          borderRadius: 1,
          flexShrink: 0,
        }}
      >
        {props.icon}
      </Box>
      <Box
        sx={{
          color: "text.secondary",
        }}
      >
        <Typography>
          <Typography
            data-testid={props.label}
            component="span"
            sx={{ fontSize: 16, fontWeight: 700, color: "text.primary", mr: 1 }}
          >
            {props.label}
          </Typography>
          <Typography component="span" sx={{ color: "grey.300", fontSize: 14, mr: 1 }}>
            @{props.handle}
          </Typography>
          {props.blocked ? (
            <Box
              sx={{
                px: 0.5,
                my: "auto",
                bgcolor: "#C9AC1233",
                borderRadius: 1,
                display: "inline-block",
                lineHeight: "18px",
                "& > svg": {
                  color: "#C9AC12",
                  fontSize: 10,
                },
              }}
            >
              <Lock />
            </Box>
          ) : null}
        </Typography>
        <Typography sx={{ fontSize: 12 }}>{props.description}</Typography>
      </Box>
    </ListItem>
  )
}
export { DatasetOption }
